

#red {
  color: white;
  border: 1px solid white;
}

.jokes li{
  font-size: 3vw;

}

.jokeDisplay{
  padding-bottom: 300px;
  display: -ms-flexbox;
  display: flex;
  -ms-flex-wrap: wrap;
      flex-wrap: wrap;
}

#active li{
  display: none;
}

#active{
  background-color: #04c2f2;
-webkit-animation-name: laser;
        animation-name: laser;
-webkit-animation-duration: 0.1s;
        animation-duration: 0.1s;
-webkit-animation-timing-function: linear;
        animation-timing-function: linear;
-webkit-animation-iteration-count: infinite;
        animation-iteration-count: infinite;
-webkit-animation-direction: alternate;
        animation-direction: alternate;
-webkit-animation-play-state: running;
        animation-play-state: running;
  }


  @-webkit-keyframes laser {
    from {background-color: #04c2f2;}
    to {background-color: red;}
}


  @keyframes laser {
    from {background-color: #04c2f2;}
    to {background-color: red;}
}


.flash:hover {
   -webkit-animation-name: flash;
           animation-name: flash;
    -webkit-animation-duration: 0.2s;
            animation-duration: 0.2s;
    -webkit-animation-timing-function: linear;
            animation-timing-function: linear;
    -webkit-animation-iteration-count: infinite;
            animation-iteration-count: infinite;
    -webkit-animation-direction: alternate;
            animation-direction: alternate;
    -webkit-animation-play-state: running;
            animation-play-state: running;
}

@-webkit-keyframes flash {
    from {color: white;}
    to {color: blue;}
}

@keyframes flash {
    from {color: white;}
    to {color: blue;}
}

h1.inactive{
display:none;
}

h1.active{
  font-size: 10em;
  padding-top: 1em;
}


img.off{
display: none ;
margin: 0 15px;
height: 20px;
width: 20px;
}

img.arrowOn{
  display: inline;
  margin: 0 15px;
  height: 20px;
  width: 20px;
  -webkit-filter: invert(100%);
}

#arrow1 {
-webkit-transform: rotate(.2turn);
    -ms-transform: rotate(.2turn);
        transform: rotate(.2turn);
}

#arrow2 {
-webkit-transform: rotate(.15turn);
    -ms-transform: rotate(.15turn);
        transform: rotate(.15turn);
}

#arrow3 {
-webkit-transform: rotate(.12turn);
    -ms-transform: rotate(.12turn);
        transform: rotate(.12turn);
}

#arrow4 {
-webkit-transform: rotate(.10turn);
    -ms-transform: rotate(.10turn);
        transform: rotate(.10turn);
}

#arrow5 {
-webkit-transform: rotate(.02turn);
    -ms-transform: rotate(.02turn);
        transform: rotate(.02turn);
}

#bottomForm{
  padding-bottom: 200px;
}


@media screen and (min-width: 895px) {
  .jokes li {
    font-size: 50px;
    padding-bottom: 2rem;
  }
}

@media screen and (max-width: 895px) {
  .jokeDisplay{
    padding-bottom: 0;
  }
}



